#header-nav {
  padding: 10px;
  margin: 0;
  align-items: center;
  border-radius: 10px;
}

#my-name {
  background-color: var(--dark1);
  opacity: .925;
  border: solid 5px var(--dark2);
  font-size: 3em;
  font-weight: 600;
  margin: 10px;
  padding: 10px 20px;
  font-family: Arial, Helvetica, sans-serif;
  color: var(--white);
  border-radius: 0.25rem;
  text-align: center;
  width: fit-content;
}

#page-nav-btns {
  justify-content: flex-end;
}

#nav-links {
  font-size: 1.5em;
  font-family: Arial, Helvetica, sans-serif;
  margin: 6px;
  font-weight: 550;
  padding: 5px;
  color: var(--white);
  background-color: var(--dark1);
  text-decoration: underline 4px var(--dark2);
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
  border-color 0.15s ease-in-out;
}

#nav-links:hover {
  background-color: var(--contrast2);
  text-decoration: underline 4px var(--dark1);
  color: var(--black);
}
.page-todo {
  font-size: 3em;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 570;
  background-color: var(--white);
  opacity: .9;
  color: var(--dark1);
  margin: 10px auto;
  padding: 10px;
  width: fit-content;
  text-align: center;
  border-radius: 10px;
}
